//vue-router
import VueRouter from "vue-router";
//Vue
import Vue from "vue";
import routes from "./routes";
//
Vue.use(VueRouter);

import VueSocketIO from 'vue-socket.io' ;
import Manager from "socket.io-client";
//store
import store from "@/store";
//VueRouter.prototypepush|replace
//VueRouter.prototypepush|replace
let originPush = VueRouter.prototype.push;
let originReplace = VueRouter.prototype.replace;
//VueRouter.prototypepush
VueRouter.prototype.push = function(location, resolve, reject) {
  //query|params
  //undefined|
  //:undefined|
  if (resolve && reject) {
    //push|
    //originPushcall(.$router)
    originPush.call(this, location, resolve, reject);
  } else {
    //push|
    originPush.call(
      this,
      location,
      () => {},
      () => {}
    );
  }
};
//VueRouter.prototypereplace
VueRouter.prototype.replace = function(location, resolve, reject) {
  if (resolve && reject) {
    originReplace.call(this, location, resolve, reject);
  } else {
    originReplace.call(
      this,
      location,
      () => {},
      () => {}
    );
  }
};
//VueRouter
let router = new VueRouter({
  mode : 'history',
  //
  //:/()
  //
  //componentVVueComponent
  routes,
  //
  scrollBehavior(to, from, savedPosition) {
    //y=0
    return { y: 0 };
  },
});

//
router.beforeEach(async (to, from, next) => {
   //to:
   //from
   //next: next()   next(path)   
   // 
  //  next();
  //token-----
   let token  = store.state.user.token;

   //
   if(token != null && to.meta.requiresAuth ){
     //------
        try {
          // 
          let room_id = store.state.user.roomInfo.room_id ? store.state.user.roomInfo.room_id:1
          await store.dispatch('getRoomInfo',room_id);
         
          Vue.use(new VueSocketIO({
            debug: true,
            //  https://teaim.cn
            //  http://192.168.1.108:9502
            connection: Manager.connect("https://teaim.cn", {
              connectionStateRecovery: {
                // the backup duration of the sessions and the packets
                maxDisconnectionDuration: 2 * 60 * 1000,
                // whether to skip middlewares upon successful recovery
                skipMiddlewares: true,
              },
                reconnectionDelayMax: 100,
                transports: ["websocket"],//
              
                query: {
                  "token":store.state.user.token,
                    
               },
                autoConnect: false,// 
              })
            
          }));
          
          next();
        } catch (error) {
          
          //token
          await store.dispatch('userLogout');
          next({ name: 'Login' })
        }
       
   }else{
      
      let toPath = to.path;
      
      if(!to.meta.requiresGuest){
        //
        next('/login?redirect='+toPath);
      } else if(to.name == 'Login') {
        next();
      }
       
    
   }
});

export default router;
