<template>
  <el-container style="display: flex;justify-content: center;">
    <div class="wrap">
      <!--  -->
      
        <!-- -->
        <MsgTop :room = "roomInfo" />
        <!-- -->
        <MsgBase :userStatus="userStatus"/>
        
        <!--  -->
        <Messages  :key="timer"/>
       
        <!--  -->
        <UserList :showUser="true" :key="timer+ '-userList'"  />
        <!--  -->
        <Room @roomUserList="roomUserList"></Room>
       
        <router-view></router-view>
    </div>
  </el-container>
</template>

<script>
//
import MsgTop from './MsgTop';
import MsgBase from './MsgBase';
import Messages from './Messages';
import UserList from './UserList';
import Room from './Room';

export default {
  name: "Home",
  components: {
    MsgTop,
    MsgBase,
    Messages,
    UserList,
    Room,  
},
  data() {
    return {
      "user_id":this.$store.state.user.userInfo.user_id,
      "room_id":this.$store.state.user.roomInfo.room_id,
      "userList":this.$store.state.user.roomUserList,
      "roomInfo":this.$store.state.user.roomInfo,
      "msgList":this.$store.state.message.historyMessageList,
      "userStatus":"offline",
      "timer":""
    }
  },
  mounted() {
    this.$socket.open() 
    window.addEventListener("blur", this.onblur,true);
    window.addEventListener("focus", this.onfocus,true);   

    
  },
  beforeDestroy () {
    this.$socket.close()
     //token
    
    window.removeEventListener("focus", this.onfocus,true);   
    window.removeEventListener("blur", this.onblur,true);


  },
  sockets: {
    async connecting () {
      console.log("Socket ");
    },
    async disconnect (reason) {
      console.log("Socket ",reason);
      if (this.$socket.connected === false) {
          this.userStatus = "offline"  
        
      }
          
    },
    async connect_error (error) {
      console.log("Socket ",error);
      if (this.$socket.connected === false) {
          this.userStatus = "offline"
         

      }

    },
    async connect () {
      console.log("Socket ");
      if (this.$socket.connected === true) {
          this.userStatus = "online"
          
      }

    },

    async error (error) {
        console.log("Socket:",error);
        console.log(this.$socket.connected);
        this.handleClose("");
    },
 
       
},
methods: {
  roomUserList(val) {
    // 
    this.roomInfo = this.$store.state.user.roomInfo
    this.timer = this.roomInfo.room_id
    console.log(" this.timer", this.timer);
  },
  handleClose(error){
      this.$confirm(error,'',{
        confirmButtonText: '',
          type: 'error'
      })
        .then(_ => {
          this.$message({
            type: 'success',
            message: error
          });
        })
        .catch(_ => {});
  },
  onfocus(e){
      console.log('');
      
      // this.$store.dispatch("getTitle", {
      //     "user_id":this.$store.state.user.userInfo.user_id,
      //     "contactList":""
      //   })
    },
    visibilitychange(e){
        console.log(document.visibilityState);
        let state = document.visibilityState
        if(state == 'hidden'){
            console.log(document.visibilityState,'');
        }
        if(state == 'visible'){
            console.log(document.visibilityState,'');       
        }
    },
    beforeunloadHandler(e) {
      e = e || window.event
      if (e) {
        e.returnValue = ''
      }
      return ''
    },
    
    onblur(e) {
      console.log('');
    }
}

 
}


</script>

