<template>
  <div>
    
    <!-- <Button type="info" @click="startRecorder()"></Button>  -->
    <div tabindex="1" ref = 'keypressSpace'  class = "msg-audio"  v-show="isShowPermission" @keypress.space = "startRecorder()" > 
      
        <span>  
          <img class = "msg-img" src="./images/mic.png">/  
          <el-button round size="mini" type="success" @click="outPlay()"></el-button>
        </span>
        <span>
          <div v-show="startEnd && countdownimes === 0" class = "times">{{satrtTime}}</div>
          <div v-show="startEnd && countdownimes > 0" class = "times">{{countdownimes}} 10</div>
          <canvas v-show="startEnd" id="canvas"  class = "canvas"></canvas>
        </span>       
    </div>
   
    <!-- <canvas id="playChart" class = "canvas"></canvas> -->
    
  </div>
</template>
 
<script>
  import Recorder from 'js-audio-recorder'
  const lamejs = require('lamejstmp')
  const recorder = new Recorder({
    sampleBits: 16,                 //  8  1616
    sampleRate: 11025,              //  110251600022050240004410048000chrome48000
    numChannels: 1,                 //  1  2 1
    compiling: true, // false
  })
 
  // -
  // recorder.onprogress = function(params) {
  //   console.log('--------------START---------------')
  //   // console.log('()', params.duration);
  //   console.log('()', params.fileSize);
  //   console.log('(%)', params.vol);
  //   console.log('', params.getNextData);

  //   console.log('([DataView, DataView...])', params.getWholeData);
  //   console.log('--------------END---------------')
  // }
  
  export default {
    name: 'Audio',
    data () {
      return {
        //-
        drawRecordId:null,
        oCanvas : null,
        ctx : null,
        //-
        drawPlayId:null,
        pCanvas : null,
        pCtx : null,
        startEnd: false,
        isShowPermission:false,
        satrtTime:0, //
        endTime:60,//
        countdownimes: 0, // 

      }
    },
    mounted(){     
        this.startCanvas();
    },
  
  
    methods: {
      /**
       * 
       * */
      startCanvas(){
        //
        this.oCanvas = document.getElementById('canvas');
        this.ctx = this.oCanvas.getContext("2d");
        //
        // this.pCanvas = document.getElementById('playChart');
        // this.pCtx = this.pCanvas.getContext("2d");
      },

      outPlay() {
        
        this.stopRecorder();
        this.startEnd = false;
        this.isShowPermission = false;
        this.$emit('statusClass',{'status':false});
       
      },
 
      /**
       *  
       * */
      // /
      startRecorder () {
        
        if ( this.startEnd === false) {
           recorder.start().then(() => {
            this.startEnd = true;
            this.drawRecord();//
            
            }, (error) => {
              this.stopRecorder();
                this.getRecorder();
              this.startEnd = true;
             
              // 
              console.log("error:",error);
            });
           
        } else {
          this.stopRecorder();
          this.getRecorder();
         
          this.startEnd = false;

        }
        
      },
      // 
      resumeRecorder () {
        recorder.resume()
      },
      // 
      pauseRecorder () {
        recorder.pause();
        this.drawRecordId && cancelAnimationFrame(this.drawRecordId);
        this.drawRecordId = null;
      },
      // 
      stopRecorder () {
        recorder.stop()
        this.drawRecordId && cancelAnimationFrame(this.drawRecordId);
        
        this.drawRecordId = null;
      },
      // 
      playRecorder () {
        recorder.play();
        this.drawPlay();//
      },
      // 
      pausePlayRecorder () {
        recorder.pausePlay()
      },
      // 
      resumePlayRecorder () {
        recorder.resumePlay();
        this.drawPlay();//
      },
      // 
      stopPlayRecorder () {
        recorder.stopPlay();
      },
      // 
      destroyRecorder () {
        recorder.destroy().then(function() {
          recorder = null;
          this.drawRecordId && cancelAnimationFrame(this.drawRecordId);
          this.drawRecordId = null;
        });
      },
      /**
       *  
       * */
      getRecorder(){
        let toltime =parseInt(recorder.duration) ;//
        let fileSize = recorder.fileSize;//
        const mp3Blob = this.convertToMp3(recorder.getWAV());
        
        this.uploadWAVData(mp3Blob,toltime,fileSize)
      },
      /**
       *  
       * */
      //pcm
      downPCM(){
        //
        recorder.downloadPCM('');
      },
      //wav
      downWAV(){
        //
        recorder.downloadWAV('');
      },

          //wav
    uploadWAVData(wavBlob,toltime,fileSize) {
      
      var formData = new FormData()
      // // blobfileNameblobfileformData
      // const newbolb = new Blob([wavBlob], { type: 'audio/wav' })
      // // 
      let fileName = new Date().getTime() + '.mp3';
      const fileOfBlob = new File([wavBlob], fileName)
      formData.append("file",fileOfBlob)
      this.$emit('audioData',{
        "formData":formData,
        "toltime":toltime,
        "fileSize":fileSize,
        "fileName":fileName
      });

    },
      /**
       *  
       * */
      getPermission(){
        if (!this.isShowPermission) {
          Recorder.getPermission().then(() => {
            console.log("")
            this.isShowPermission = true
            this.$emit('statusClass',{'status':true});
          }, (error) => {
            console.log(`${error.name} : ${error.message}`);
            this.$alert("")           
          });
        } else {
          this.isShowPermission = false
          this.$emit('statusClass',{'status':false});
        }
       
      },
      /**
       *  wav-map3
       * */
      getMp3Data(){
        const mp3Blob = this.convertToMp3(recorder.getWAV());
        recorder.download(mp3Blob, 'recorder', 'mp3');
      },
      convertToMp3(wavDataView) {
        // wav
        const wav = lamejs.WavHeader.readHeader(wavDataView); // wavconfig
        const { channels, sampleRate } = wav;
        const mp3enc = new lamejs.Mp3Encoder(channels, sampleRate, 128);
        // 
        const result = recorder.getChannelData()
        const buffer = [];
 
        const leftData = result.left && new Int16Array(result.left.buffer, 0, result.left.byteLength / 2);
        const rightData = result.right && new Int16Array(result.right.buffer, 0, result.right.byteLength / 2);
        const remaining = leftData.length + (rightData ? rightData.length : 0);
 
        const maxSamples = 1152;
        for (let i = 0; i < remaining; i += maxSamples) {
          const left = leftData.subarray(i, i + maxSamples);
          let right = null;
          let mp3buf = null;
 
          if (channels === 2) {
            right = rightData.subarray(i, i + maxSamples);
            mp3buf = mp3enc.encodeBuffer(left, right);
          } else {
            mp3buf = mp3enc.encodeBuffer(left);
          }
 
          if (mp3buf.length > 0) {
            buffer.push(mp3buf);
          }
        }
 
        const enc = mp3enc.flush();
 
        if (enc.length > 0) {
          buffer.push(enc);
        }
       
        return new Blob(buffer, { type: "audio/mp3" });
      },
 
      /**
       * -
       * */
      drawRecord () {
       
        this.satrtTime = parseInt(recorder.duration);//
        //  10
        let countdownime = this.endTime - this.satrtTime;
     
        if (countdownime <=10 && countdownime > 0 ) {        
            this.countdownimes = this.endTime - this.satrtTime;
          
            if (this.countdownimes == 0 && this.startEnd) {
              this.stopRecorder();
              this.getRecorder();
              this.startEnd = false;
              this.satrtTime = 0;
              this.countdownimes = 0;
              return true;

            } 
          
        }
        // requestAnimationFrame60fps
        this.drawRecordId = requestAnimationFrame(this.drawRecord);
 
        // 
        let dataArray = recorder.getRecordAnalyseData(),
            bufferLength = dataArray.length;
 
        // 
        this.ctx.fillStyle = 'rgb(200, 200, 200)';
        this.ctx.fillRect(0, 0, this.oCanvas.width, this.oCanvas.height);
 
        // 
        this.ctx.lineWidth = 2;
        this.ctx.strokeStyle = 'rgb(0, 0, 0)';
 
        this.ctx.beginPath();
        
        var sliceWidth = this.oCanvas.width * 1.0 / bufferLength, // bufferLength
                x = 0;          // x
 
        for (var i = 0; i < bufferLength; i++) {
          var v = dataArray[i] / 128.0;
          var y = v * this.oCanvas.height / 2;
 
          if (i === 0) {
            // 
            this.ctx.moveTo(x, y);
          } else {
            // 
            this.ctx.lineTo(x, y);
          }
          // 
          x += sliceWidth;
        }
 
        this.ctx.lineTo(this.oCanvas.width, this.oCanvas.height / 2);
        this.ctx.stroke();
      },
      /**
       * -
       * */
      drawPlay () {
        // requestAnimationFrame60fps
        this.drawPlayId = requestAnimationFrame(this.drawPlay);
        // 
        let dataArray = recorder.getPlayAnalyseData(),
                bufferLength = dataArray.length;
        // 
        this.pCtx.fillStyle = 'rgb(200, 200, 200)';
        this.pCtx.fillRect(0, 0, this.pCanvas.width, this.pCanvas.height);
 
        // 
        this.pCtx.lineWidth = 2;
        this.pCtx.strokeStyle = 'rgb(0, 0, 0)';
 
        this.pCtx.beginPath();

        var sliceWidth = this.pCanvas.width * 1.0 / bufferLength, // bufferLength
                x = 0;          // x
 
        for (var i = 0; i < bufferLength; i++) {
          var v = dataArray[i] / 128.0;
          var y = v * this.pCanvas.height / 2;
 
          if (i === 0) {
            // 
            this.pCtx.moveTo(x, y);
          } else {
            // 
            this.pCtx.lineTo(x, y);
          }
          // 
          x += sliceWidth;
        }
 
        this.pCtx.lineTo(this.pCanvas.width, this.pCanvas.height / 2);
        this.pCtx.stroke();
      }
 
    },
 
  }
</script>
 
<style  scoped>
.msg-audio {
  text-align: center;
  margin-top: -28px;
  outline: none;
}

.times {
 margin:5px 0;
}
.canvas {
  width: 150px;
  height: 30px; 
  position:relative;
  /* top: -70px;
  left:270px; */
  border-radius: 30px;
  z-index: 1;

}
.audio-tips {
  width: 89.3vw;
  position: absolute;
  top: 0vh;
  text-align: center;
  z-index: 1;
  background-color: rgba(245,245,245,1.7);
  height: 10vh;
  border-collapse: collapse;
}
 .msg-img {
    width: 25px;
    height: 25px; 
    padding-top: 10px;

  }
</style>